export default {
	data() {
		return {
			items: [
				'选项卡1',
				'选项卡2',
				'选项卡3'
			],
			styles: [{
				value: 'button',
				text: '按钮',
				checked: true
			}, {
				value: 'text',
				text: '文字'
			}],
			colors: [
				'#007aff',
				'#4cd964',
				'#dd524d'
			],
			current: 0,
			activeColor: '#007aff',
			styleType: 'button'
		}
	},
	components: {
		segmentedControl
	},
	methods: {
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		styleChange(evt) {
			if (this.styleType !== evt.target.value) {
				this.styleType = evt.target.value;
			}
		},
		colorChange(evt) {
			if (this.styleType !== evt.target.value) {
				this.activeColor = evt.target.value;
			}
		}
	}
}



